import React from "react";
import { useLocation } from "react-router";

export default function Search() {
  const location = useLocation();
  let search = new URLSearchParams(location.search);
  return (
    <h1>
      {search.get("param")
        ? JSON.stringify(search.get("param")) + ""
        : "  "}
    </h1>
  );
}
